import React from "react";
import BottomTabNav from "../../customs/BottomTabNav";
import DynamicImageFields from "../../customs/DynamicImageFields";
import ImageField from "../../customs/ImageField";


export default function Documents(props) {
  return (
    <fieldset>
      <ImageField
        name="restaurant_image"
        title="Profile Image"
        id="restaurant_image"
        minSize="800 x 800"
        accepts={[".jpeg", ".png", ".jpg"]}
      />
      <ImageField
        name="banner_image"
        title=" Banner Image"
        id="banner_image"
        minSize="1024 x 576"
        accepts={[".jpeg", ".png", ".jpg"]} />

      <DynamicImageFields title="Document Name" />
      <BottomTabNav currentStep={2} props={props} />
    </fieldset>
  );
}
