import React, { useState } from "react";
import Input from "../../../customs/Input"
import SelectOption from "../../../customs/SelectOption";
import DynamicAdOns from "./DynamicAdOns";

export default function DynamicMeal({ day, index, addMeal }) {
    const [addOns, setaddOns] = useState([]);

    const [meal, setMeal] = useState({
        meal_name: "",
        day: day,
        slot: "",
        type: "",
        description: "",
        image: "",
        add_on: addOns,
    });
    const [mealImage, setMealImage] = useState("");
    const [loading, setLoading] = useState(false);
    const onChangeText = (index, event) => {
        const { name, value } = event.target;
        setMeal((prevState) => ({ ...prevState, [name]: value }));
    };
    const onImageChange = () => { }

    const done = () => {
        addMeal(meal);
        setLoading(true);
    };
    return (
        <fieldset key={index}>
            <div className="row mt-1">
                <div className="col-12 justify-content-end">
                    <button
                        type="button"
                        className="btn btn-primary float-right mr-2 "
                        onClick={done}
                    >
                        {loading ? "✔" : "Save"}
                    </button>
                </div>
            </div>

            <div className="row mt-2">
                <div className="col-4">
                    <SelectOption
                        label="available as"
                        required={true}
                        name="slot"
                        values={[
                            { name: 'Choose an option', value: '' },
                            { name: 'Lunch', value: 'Lunch' },
                            { name: 'Dinner', value: 'Dinner' }
                        ]}
                        handler={(e) => onChangeText(index, e)}
                    />
                </div>
                <div className="col-4">
                    <SelectOption
                        label="available as"
                        required={true}
                        name="slot"
                        values={[
                            { name: 'Choose an option', value: '' },
                            { name: 'Veg', value: 'veg' },
                            { name: 'Non-Veg', value: 'non-veg' }
                        ]}
                        handler={(e) => onChangeText(index, e)}
                    />
                </div>
                <div className="col-4">
                    <Input
                        label="meal name"
                        type="text"
                        name="meal_name"
                        required={true}
                        handler={(e) => onChangeText(index, e)}
                    />
                </div>
            </div>
            <div className="row mt-2">
                <div className="col-4">
                    <div className="form-group">
                        <label>Meal Image</label>
                        <div className="custom-file">
                            <input
                                id="logo"
                                type="file"
                                className="custom-file-input"
                                onChange={(e) => onImageChange(index, e)}
                            />
                            <label htmlFor="logo" className="custom-file-label">
                                {mealImage}
                            </label>
                        </div>
                    </div>
                </div>
                <div className="col-8">
                    <div className="form-group">
                        <label>
                            Meal Description <strong className="text-danger">*</strong>
                        </label>
                        <textarea
                            className="form-control form-control-plaintext"
                            name="description"
                            onChange={(e) => onChangeText(index, e)}
                        />
                    </div>
                </div>
            </div>
            <DynamicAdOns day={day} handleChangeInput={onChangeText} />
        </fieldset>
    );
}
