import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";

export default function Plans(props) {
  const [plan, setPlan] = useState([]);
  const [inputPlan, setInputPlan] = useState([{
    plan_name: "",
    base_price: "",
    customer_price: "",
    delivery_price: ""
  }]);
  const [loaded, setLoaded] = useState(false)
  const restaurant = useSelector((state) => state.restaurant);
  const [isDelivery, setIsDelivery] = useState(false)
  const { plans } = useSelector((state) => state.plans)
  const [category, setCategory] = useState("Lunch")
  const [price_plan, setPricePlan] = useState({
    restaurant_id: "",
    isDelivery: false,
    price_plans: [
      {
        category: "Lunch",
        plans: inputPlan
      },
      {
        category: "Dinner",
        plans: inputPlan
      },
    ]
  })
  const dispatch = useDispatch();

  useEffect(() => {
    setPlan(plans)
  }, [plans])
  // Set Plan information

  const getPlansAndSet = async () => {
    let inputPlans = []
    let elements = Array.isArray(plan) && plan.length
    for (let index = 0; index < elements; index++) {
      let plan = {
        plan_name: plans[index].plan_name,
        base_price: 0,
        customer_price: plans[index].profit_margin,
        delivery_price: 0
      }
      inputPlans.push(plan)
    }
    setInputPlan(inputPlans)
    setLoaded(true)
  }
  // Initialize a plan with initial information
  useEffect(() => {
    let componentMounted = true
    if (componentMounted) {
      getPlansAndSet()
    }
    return () => {
      componentMounted = false
    }
  }, [plan])
  // Initial render with default data

  const onBasePriceChange = (event, index, slot) => {
    setCategory(slot)
    const { name, value } = event.target
    let plans = inputPlan
    let currPlan = plans[index]
    currPlan.customer_price = parseFloat(parseFloat(currPlan.base_price) + parseFloat(plan[index].profit_margin)).toString()
    currPlan.base_price = value
    plans.splice(index, 1, currPlan)

    setInputPlan(plans)
  }
  // Change Base Price and customer price

  const setPricePlans = () => {
    const abc = price_plan.price_plans.find((o) => o.category === category)
    console.log('====================================');
    console.log(abc);
    console.log('====================================');
    const price_plans = {
      isDelivery,
      price_plans: [
        {
          category,
          plans: [...inputPlan]
        }
      ]
    }
    setPricePlan(price_plans)
  }

  useEffect(() => {
    setPricePlans()
  }, [category])


  const onDeliveryChange = (event, index) => {
    const { name, value } = event.target
    let plans = inputPlan
    let currPlan = plans[index]
    currPlan.delivery_price = value
    plans.splice(index, 1, currPlan)
    setInputPlan(plans)
  }
  // Change Delivery Price




  const handleContinue = (e) => {
    // const plans = {
    //   ...restaurant,
    //   ...inputPlan,
    // };
    // dispatch({
    //   type: SET_PLANS,
    //   payload: plans,
    // });

    // props.goToStep(5);
  };

  const handleBack = (e) => {
    props.goToStep(3);
  };
  // Previous Button




  return (
    <fieldset>
      {/* {
        price_plan.price_plans.map((plan, key) => (
            
        ))
      } */}
      <div className="row">
        <div className="col-lg-12 justify-content-end">
          <button
            type="button"
            className="btn btn-primary float-right mr-2 "
            onClick={handleContinue}
          >
            Next
          </button>
          <button
            type="button"
            className="btn btn-default float-right mr-2 "
            onClick={handleBack}
          >
            Previous
          </button>
        </div>
      </div>
    </fieldset>
  );
}
