import React, { useState } from "react";
import BottomTabNav from "../../customs/BottomTabNav";
import Input from '../../customs/Input'
import SelectOption from "../../customs/SelectOption";


export default function BasicInfo(props) {
  const [cuisines, setCuisines] = useState([]);
  const [chef, setChef] = useState({});

  const onChangeText = ({ target }) => {
    setChef((prevState) => ({ ...prevState, [target.name]: target.value }));
  };

  return (
    <fieldset>
      <div className="row mt-2">
        <div className="col-lg-4">
          <Input label="restaurant name"
            required={true}
            name="restaurant_name"
            type="text"
            handler={onChangeText} />
          <Input label="owner name"
            required={true}
            name="owner_name"
            type="text"
            handler={onChangeText} />
          <Input label="City"
            required={true}
            name="city"
            type="text"
            handler={onChangeText} />
          <Input label="country"
            required={true}
            name="country"
            type="text"
            handler={onChangeText} />
        </div>
        <div className="col-lg-4">
          <Input label="phone number"
            required={true}
            name="phone"
            type="text"
            handler={onChangeText} />

          <Input label="address line 1"
            required={true}
            name="locality"
            type="text"
            handler={onChangeText} />

          <Input label="state"
            required={true}
            name="states"
            type="text"
            handler={onChangeText} />

          <Input label="commission"
            required={true}
            name="commission"
            type="text"
            handler={onChangeText} />
        </div>

        <div className="col-lg-4">
          <Input label="email id"
            required={true}
            name="email"
            type="text"
            handler={onChangeText} />
          <Input label="address line 2"
            required={false}
            name="states"
            type="text"
            handler={onChangeText} />

          <Input label="postal code"
            required={true}
            name="postal_code"
            type="text"
            handler={onChangeText} />
          <SelectOption label="meal type"
            required={true}
            handler={onChangeText}
            name="meal_type"
            values={[{ name: "Choose an option", value: '' }, { name: 'Veg', value: 'Veg' }, { name: 'Non-Veg', value: 'Non-Veg' }]}
          />
        </div>
      </div>


      <div className="row mt-2">
        <div className="col-lg-4">
          <SelectOption
            label="Cuisine"
            name="cuisine_type"
            handler={onChangeText}
            required={true}
            values={cuisines}
          />
          <SelectOption
            label="meal time"
            name="category"
            handler={onChangeText}
            required={true}
            values={[
              { name: "Choose an option", value: '' },
              { name: 'Both', value: 'Both' },
              { name: 'Lunch', value: 'Lunch' },
              { name: 'Dinner', value: 'Dinner' }]}
          />
        </div>
        <div className="col-lg-8" aria-rowspan={2}>
          <div className="form-group">
            <label>
              ABOUT <strong className="text-danger">*</strong>
            </label>
            <textarea
              className="form-control form-control-plaintext"
              name="about"
              onChange={(e) => onChangeText(e)}
              rows={4}
            />
          </div>
        </div>
      </div>
      <BottomTabNav currentStep={1} props={props} data={chef} />
    </fieldset>
  );
}
