import React, { useState, useEffect } from 'react'
import Input from '../../../customs/Input';

export default function DynamicAdOns({ day, handleChangeInput }) {
    const [addOns, setaddOns] = useState([
        {
            add_on: "",
            add_on_price: "",
            add_on_image: "",
        },
    ]);
    const addInputFields = () => {
        setaddOns([...addOns, { add_on: "", add_on_price: "", add_on_image: "" }]);
    };
    const handleRemoveClicked = (index) => {
        const values = [...addOns];
        values.splice(index, 1);
        setaddOns(values);
    };
    return (
        addOns.map((inputfield, index) => (
            <div className="row mt-2" key={index}>
                <div className="col-3">
                    <Input
                        label="Add on"
                        name="add_on"
                        type="text"
                        handler={(e) => handleChangeInput(index, e)}
                        required={false}
                    />
                </div>
                <div className="col-3">
                    <Input
                        label="price"
                        name="add_on_price"
                        type="text"
                        handler={(e) => handleChangeInput(index, e)}
                        required={false}
                    />
                </div>
                <div className="col-4">
                    <div className="form-group">
                        <label>Add On Image</label>
                        <div className="custom-file">
                            <input
                                id="logo"
                                type="file"
                                className="custom-file-input"
                                name="add_on_image"
                                onChange={(event) => handleChangeInput(index, event)}
                            />
                            <label htmlFor="logo" className="custom-file-label">
                                Choose File...
                            </label>
                        </div>
                    </div>
                </div>
                <div className="col-2">
                    <div className="form-inline mt-4 py-2">
                        <button
                            type="button"
                            className="btn btn-default mr-2"
                            onClick={(event) => addInputFields(event)}
                        >
                            <i className="fa fa-plus" />
                        </button>
                        <button
                            type="button"
                            className="btn btn-danger"
                            onClick={() => handleRemoveClicked(index)}
                        >
                            <i className="fa fa-trash-o" />
                        </button>
                    </div>
                </div>
            </div>
        ))

    )
}
